import React, { Component } from 'react'

   export class Logo1 extends Component {
    render() {
        return (
            <a className="logo-container" href='/'>
                 <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" viewBox="0 0 100% 100%">
      <defs>
        <pattern id="pattern" preserveAspectRatio="none" width="100%" height="100%" viewBox="0 0 140 119">
          <image  xlinkHref="data:image/png;base64,"/>
        </pattern>
      </defs>
      <rect id="logo" width="99" height="84" fill="url(#pattern)"/>
    </svg>
            </a>
        )
    }
}



export class Logo2 extends Component {
  render() {
    return (
      
      <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" viewBox="0 0 234 199" className="logo2-svg" aspect-ratio="XminYmin" preserveAspectRatio="none">
      <image id="logo" width="234" height="199" opacity="0.25" xlinkHref="data:image/png;base64,"/>
      </svg>
    
    )
  }
}




